<template>
    <form  enctype="multipart/form-data" class="flex items-center" >
                    <div class="flex items-center column">
                        <input v-if="!isSended"  @change="handleChange" name="documents" accept="image/*" type="file" id="imgupload" style="display:none"/>
                        <label   for="imgupload">
                          <img  class="slider-applicant__img cursor-pointer pulse" src="../assets/document.png" alt="">
                        </label> 
                        <span class="text-info n-mt-xs">(Click on the image to upload files.)</span>
                    </div>
                    <ul class="flex column">
                        <li v-for="(document,index) in documentsData" :key="index" @click="removeFile(index)" class="flex items-center cursor-pointer n-mb-xs">
                              <f-icon :class="{'upload__check':true,'success':document.isUpload}" :icon="['far', document.isUpload?'check-square' :'square']"/>
                              <p :class="{'upload__p':true,'n-ml-xs':true,'success':document.isUpload}">Document {{index}}</p>
                        </li> 
                        <!-- <li v-for="document in [1,2,3,4]"  :key="document" class="flex items-center cursor-pointer n-mb-xs">
                            <f-icon :class="{'upload__check':true,'success':documents.length>=document}" :icon="['far', documents.length>=document?'check-square' :'square']"/>
                            <p :class="{'upload__p':true,'n-ml-xs':true,'success':documents.length>=document}">Document {{document}}</p>
                        </li> -->
                         <!-- <li class="flex items-center">
                            <f-icon class="success"  :icon="['far','check-square']"/>
                            <p class="n-ml-xs success">Document 2</p>
                        </li>
                         <li class="flex items-center">
                            <f-icon :icon="['far','square']"/>
                            <p class="n-ml-xs">Document 3</p>
                        </li>
                         <li class="flex items-center">
                            <f-icon :icon="['far','square']"/>
                            <p class="n-ml-xs">Document 4</p>
                        </li> -->
                    </ul>
                    <!-- <button class="btn btn__upload">UPLOAD</button> -->
    </form> 
</template>

<script>
export default {
  data() {
    return {
      form: new FormData(),
      //   documentIndex: 0,
      documents: [],
      documentsData: [
        { isUpload: false },
        { isUpload: false },
        { isUpload: false },
        { isUpload: false }
      ],
      lastRemoveIndex: null,
      isSended: false
    };
  },
  methods: {
    handleChange($event) {
      const file = $event.target.files[0],
        name = file.name.split(".")[0];
      if (this.documents.length > 3) return;

      if (typeof this.lastRemoveIndex === "number") {
        this.documents[this.lastRemoveIndex] = { name, file };
        this.documentsData[this.lastRemoveIndex].isUpload = true;
        this.lastRemoveIndex = null;
      } else {
        this.documentsData[this.documents.length].isUpload = true;
        this.documents.push({ name, file });
      }
      // this.lastRemoveIndex = null;

      console.log(this.documents);
    },
    removeFile(index) {
      if (!this.lastRemoveIndex) {
        this.lastRemoveIndex = index;
        this.documentsData[index].isUpload = false;
        this.documents[index] = null;
      } else {
        alert("AGUANTAAA!");
      }
      // console.log("Removing " + index);
    }
  }
};
</script>

<style lang="scss">
.upload__ {
  &p,
  &check {
    transition: all ease 0.5s;
  }
}
</style>
